import React, { useState, useReducer } from "react"
import Autosuggest from "react-autosuggest"
import axios from "axios"
import AutosuggestHighlightMatch from "autosuggest-highlight/match"
import AutosuggestHighlightParse from "autosuggest-highlight/parse"
import moment from "moment"
import "./search.css"
import EpList from "./eps.json"

const randomQuery = [
  "santa",
  "movie",
  "teaser",
  "chef kevin",
  "moriarty",
  "earwolf",
  "scoop troop",
  "You're Doing It Wrong: By Ho Ho Hove I Think Eggnog It",
  "gmail roulette",
  "speak on that",
  "ice bucket challenge",
  "cards against humanity",
  "engineer cody boy",
  "i love you and i'm in love with you",
  "guardians of the galaxy",
]
function getRandomInt(max: number) {
  return Math.floor(Math.random() * Math.floor(max))
}

const Search = (props: any) => {
  let initValue = randomQuery[getRandomInt(randomQuery.length)]
  const [value, setValue] = useState(initValue)
  const [suggestions, setSuggestions] = useState([])
  const [exact, setExact] = useState(false)
  const [showExactInfo, setShowExactInfo] = useState(false)
  const [, forceUpdate] = useReducer(x => x + 1, 0)

  const secToMins = (seconds: number) => {
    let minutes: number = seconds / 60
    let totMins: string = minutes.toFixed(2)
    var number = totMins.replace(".", ":")
    return number
  }

  const ExtLink = (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      opacity="0.7"
      fill="#3182ce"
      viewBox="0 0 768 1024"
    >
      <defs />
      <path d="M640 768H128V258l128-2V128H0v768h768V576H640v192zM384 128l128 128-192 192 128 128 192-192 128 128V128H384z" />
    </svg>
  )

  const onSuggestionFetchFuzzy = async ({ value }: { value: string }) => {
    await axios
      .post(
        "https://search-podcast-indexer-4gzssr2fnp27pbrx5fbmc4rml4.us-east-1.es.amazonaws.com/paragraphs/_search",
        {
          from: 0,
          size: 99,
          query: {
            multi_match: {
              query: value,
              fields: ["text", "Episode"],
              fuzziness: "AUTO",
            },
          },
        }
      )
      .then(res => {
        const results = res.data.hits.hits.map((h: any) => h._source)
        setSuggestions(() => results)
      })
  }

  const onSuggestionFetchExact = async ({ value }: { value: string }) => {
    await axios
      .post(
        "https://search-podcast-indexer-4gzssr2fnp27pbrx5fbmc4rml4.us-east-1.es.amazonaws.com/paragraphs/_search",
        {
          from: 0,
          size: 99,
          query: {
            multi_match: {
              query: value,
              fields: ["text", "Episode"],
              type: "phrase",
              operator: "and",
            },
          },
        }
      )
      .then(res => {
        const results = res.data.hits.hits.map((h: any) => h._source)
        setSuggestions(() => results)
      })
  }

  const onSuggestionsClearRequested = () => {
    return null
  }

  const renderSuggestion = (suggestion: any, { query }: { query: any }) => {
    let epDetails: any = EpList.find(x => x.title === suggestion.Episode)
    let date = moment(epDetails.pubDate)
    const suggestionText = suggestion.text
    const matches = AutosuggestHighlightMatch(suggestionText, query)
    const parts = AutosuggestHighlightParse(suggestionText, matches)

    return (
      <div className="min-w-full px-4 pt-6 pb-6 mb-6 border-l-4 border-white shadow-md hover:border-gray-400">
        <div className="flex flex-wrap items-center justify-between w-full mb-2">
          <div className="flex items-center">
            <div className="pt-1 mr-2 text-base text-gray-700">
              {suggestion.Episode} ({date.format('YYYY MMM DD')})
            </div>
          </div>
          <div className="flex items-center font-mono text-right text-gray-600">
            <div className="hidden md:inline">time: </div>
            {secToMins(suggestion.startTime)}&nbsp;
            <div className="mr-2 font-sans text-right text-blue-600 border-b-2 border-dotted">
              <a
                href={`https://www.stitcher.com/search?q=${suggestion.Episode}#episodes`}
              >
                Find on Stitcher{" "}
                <div className="inline-block w-3 h-3 mb-px">{ExtLink}</div>
              </a>
            </div>
          </div>
        </div>
        <div className="px-2 py-2 mt-4 border-l-2 border-gray-400 md:px-6 md:text-lg">
          {parts.map((part: any, index: number) => {
            const className = part.highlight ? "highlight" : ""

            return (
              <span className={className} key={index}>
                {part.text.replace(/'/g, "")}
              </span>
            )
          })}
        </div>
      </div>
    )
  }

  const getSuggestionValue = (suggestions: any) => value

  const inputProps = {
    placeholder: "Search",
    value,
    autoFocus: true,
    onChange: (_: any, { newValue }: any) => setValue(() => newValue),
  }

  const handleCheckbox = () => {
    setExact(prev => !prev)
    forceUpdate()
    setShowExactInfo(() => true)
    setTimeout(() => {
      setShowExactInfo(() => false)
    }, 2000)
  }

  const handleRandom = () => {
    setValue(() => randomQuery[getRandomInt(randomQuery.length)])
    setShowExactInfo(() => true)
    setTimeout(() => {
      setShowExactInfo(() => false)
    }, 2000)
  }

  return (
    <div>
      <div className="flex flex-wrap items-center px-2 mt-2 md:px-2 md:mt-2">
        <button
          className="px-2 mr-4 text-sm text-white bg-blue-700 rounded"
          onClick={handleRandom}
        >
          Get random query
        </button>

        <label className="mr-4 text-sm" htmlFor="check">
          Search exact matches:{" "}
        </label>

        <input
          id="check"
          type="checkbox"
          onChange={handleCheckbox}
          checked={exact}
        />
        {showExactInfo && (
          <div className="px-2 my-2 text-sm text-white bg-red-700 rounded md:mx-2 md:my-0">
            Click the search bar
          </div>
        )}
        <div className="flex flex-wrap w-full mt-2 mb-2 text-sm">
          {suggestions.length === 99 ? (
            <p>99+ results for&nbsp;</p>
          ) : (
            <p>{suggestions.length} results for&nbsp;</p>
          )}
          {value && <p>'{value}'&nbsp;</p>}
          {exact ? <p>(exact search)</p> : <p>(fuzzy search)</p>}
        </div>
      </div>
      {/* <form> */}
        <Autosuggest
          suggestions={suggestions}
          onSuggestionsFetchRequested={
            exact ? onSuggestionFetchExact : onSuggestionFetchFuzzy
          }
          onSuggestionsClearRequested={onSuggestionsClearRequested}
          getSuggestionValue={getSuggestionValue}
          renderSuggestion={renderSuggestion}
          inputProps={inputProps}
          focusInputOnSuggestionClick={false}
          alwaysRenderSuggestions={true}
          // autoFocus
        />
      {/* </form> */}
    </div>
  )
}

export default Search
